
"use client";

import * as React from "react";
import { useRouter } from "next/navigation";
import { Camera, Loader2, Compass, Check, X } from "lucide-react";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";

import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent } from "@/components/ui/card";
import { useToast } from "@/hooks/use-toast";
import {
  AlertDialog,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogDescription,
} from "@/components/ui/alert-dialog";

// Schema for employee login form validation.
const employeeFormSchema = z.object({
  employeeId: z.string().min(1, "Employee ID is required"),
  password: z.string().min(1, "Password is required"),
});

// Schema for HR login form validation.
const hrFormSchema = z.object({
  email: z.string().email("Invalid email address"),
  password: z.string().min(1, "Password is required"),
});

// Schema for Admin login form validation.
const adminFormSchema = z.object({
  email: z.string().email("Invalid email address"),
  password: z.string().min(1, "Password is required"),
});

type GeofenceStatus = "idle" | "getting_location" | "verifying" | "success" | "error";

/**
 * Renders a login form with tabs for "Employee", "HR Admin", and "Admin".
 * Handles form submission, validation, and different login methods including geofenced clock-in.
 * @returns {JSX.Element} The login form component.
 */
export function LoginForm() {
  const router = useRouter();
  const { toast } = useToast();
  const [geofenceStatus, setGeofenceStatus] = React.useState<GeofenceStatus>("idle");
  const [geofenceError, setGeofenceError] = React.useState<string | null>(null);
  const [isLoading, setIsLoading] = React.useState(false);

  // Form hook for employee login.
  const employeeForm = useForm<z.infer<typeof employeeFormSchema>>({
    resolver: zodResolver(employeeFormSchema),
    defaultValues: { employeeId: "", password: "" },
  });

  // Form hook for HR login.
  const hrForm = useForm<z.infer<typeof hrFormSchema>>({
    resolver: zodResolver(hrFormSchema),
    defaultValues: { email: "", password: "" },
  });

  // Form hook for Admin login.
  const adminForm = useForm<z.infer<typeof adminFormSchema>>({
    resolver: zodResolver(adminFormSchema),
    defaultValues: { email: "", password: "" },
  });

  /**
   * Handles the geofenced clock-in process.
   * Gets user's location and simulates verification.
   */
  const handleClockIn = async () => {
    setGeofenceStatus("getting_location");
    setGeofenceError(null);

    if (!navigator.geolocation) {
        setGeofenceError("Geolocation is not supported by your browser.");
        setGeofenceStatus("error");
        return;
    }

    navigator.geolocation.getCurrentPosition(
        async (position) => {
            const { latitude, longitude } = position.coords;
            setGeofenceStatus("verifying");

            // --- SERVER INTEGRATION POINT ---
            // In a real application, you would send these coordinates to your server/API
            // to verify if they are within the allowed geofence.
            // Example: const response = await fetch('/api/verify-location', {
            //   method: 'POST',
            //   body: JSON.stringify({ latitude, longitude, employeeId: employeeForm.getValues("employeeId") })
            // });
            // const result = await response.json();

            // Simulating server verification delay
            await new Promise(resolve => setTimeout(resolve, 2000));
            const isWithinGeofence = true; // Simulate a successful check

            if (isWithinGeofence) {
                setGeofenceStatus("success");
                toast({
                    title: "Clock-In Successful",
                    description: "Your attendance has been recorded.",
                });
                // In a real app, you might wait a moment before redirecting
                await new Promise(resolve => setTimeout(resolve, 1500));
                router.push("/dashboard");
            } else {
                setGeofenceError("You are not within the allowed geofence for clock-in.");
                setGeofenceStatus("error");
            }
        },
        (error) => {
            let message = "An unknown error occurred while getting your location.";
            if (error.code === error.PERMISSION_DENIED) {
                message = "Location access denied. Please enable location permissions to clock in.";
            } else if (error.code === error.POSITION_UNAVAILABLE) {
                message = "Your location information is currently unavailable.";
            }
            setGeofenceError(message);
            setGeofenceStatus("error");
        }
    );
  };
  
  /**
   * Handles standard employee login.
   * Simulates an API call and navigates to the dashboard on success.
   */
  const handleStandardLogin = async () => {
    setIsLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 1000));
    setIsLoading(false);
    router.push("/dashboard");
  }

  /**
   * Handles HR admin login.
   * Simulates an API call and navigates to the HR dashboard on success.
   */
  const handleHrLogin = async () => {
    setIsLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 1000));
    setIsLoading(false);
    router.push("/hr/dashboard");
  }

  /**
   * Handles admin login.
   * Simulates an API call and navigates to the admin dashboard on success.
   */
  const handleAdminLogin = async () => {
    setIsLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 1000));
    setIsLoading(false);
    router.push("/admin/dashboard");
  }
  
  const isClockingIn = geofenceStatus !== "idle" && geofenceStatus !== "success";

  return (
    <>
      <Tabs defaultValue="employee" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="employee">Employee</TabsTrigger>
          <TabsTrigger value="hr">HR Admin</TabsTrigger>
          <TabsTrigger value="admin">Admin</TabsTrigger>
        </TabsList>
        {/* Employee Login Tab */}
        <TabsContent value="employee">
          <Card>
            <CardContent className="p-6">
              <Form {...employeeForm}>
                <form onSubmit={employeeForm.handleSubmit(handleStandardLogin)} className="space-y-4">
                  <FormField
                    control={employeeForm.control}
                    name="employeeId"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Employee ID</FormLabel>
                        <FormControl>
                          <Input placeholder="e.g., 10023" {...field} disabled={isClockingIn} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={employeeForm.control}
                    name="password"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Password</FormLabel>
                        <FormControl>
                          <Input type="password" placeholder="••••••••" {...field} disabled={isClockingIn}/>
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <div className="space-y-2 pt-2">
                     <Button onClick={handleClockIn} type="button" className="w-full" size="lg" disabled={isClockingIn}>
                        {isClockingIn ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Camera className="mr-2 h-4 w-4" />}
                        Clock In (Geofenced)
                    </Button>
                    <Button type="submit" variant="outline" className="w-full" disabled={isLoading || isClockingIn}>
                      {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                      Standard Login
                    </Button>
                  </div>
                </form>
              </Form>
            </CardContent>
          </Card>
        </TabsContent>
        {/* HR Admin Login Tab */}
        <TabsContent value="hr">
          <Card>
            <CardContent className="p-6">
              <Form {...hrForm}>
                <form onSubmit={hrForm.handleSubmit(handleHrLogin)} className="space-y-4">
                  <FormField
                    control={hrForm.control}
                    name="email"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Email</FormLabel>
                        <FormControl>
                          <Input placeholder="hr@example.com" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={hrForm.control}
                    name="password"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Password</FormLabel>
                        <FormControl>
                          <Input type="password" placeholder="••••••••" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <div className="pt-2">
                    <Button type="submit" className="w-full" size="lg" disabled={isLoading}>
                      {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                      Login to HR Portal
                    </Button>
                  </div>
                </form>
              </Form>
            </CardContent>
          </Card>
        </TabsContent>
        {/* Admin Login Tab */}
        <TabsContent value="admin">
          <Card>
            <CardContent className="p-6">
              <Form {...adminForm}>
                <form onSubmit={adminForm.handleSubmit(handleAdminLogin)} className="space-y-4">
                  <FormField
                    control={adminForm.control}
                    name="email"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Email</FormLabel>
                        <FormControl>
                          <Input placeholder="admin@example.com" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={adminForm.control}
                    name="password"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Password</FormLabel>
                        <FormControl>
                          <Input type="password" placeholder="••••••••" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <div className="pt-2">
                    <Button type="submit" className="w-full" size="lg" disabled={isLoading}>
                      {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                      Login to Admin Portal
                    </Button>
                  </div>
                </form>
              </Form>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
      {/* Dialog for Clock-In Process */}
      <AlertDialog open={isClockingIn} onOpenChange={(open) => { if(!open) setGeofenceStatus("idle"); }}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle className="text-center">Geofenced Clock-In</AlertDialogTitle>
            <AlertDialogDescription asChild>
              <div className="text-center text-muted-foreground space-y-4">
                  {geofenceStatus === 'getting_location' && (
                      <>
                          <div className="flex justify-center my-4">
                              <Compass className="w-12 h-12 animate-pulse text-primary"/>
                          </div>
                          <p>Requesting location access... Please approve the request in your browser.</p>
                      </>
                  )}
                  {geofenceStatus === 'verifying' && (
                      <>
                           <div className="flex justify-center my-4">
                                <Loader2 className="w-12 h-12 animate-spin text-primary"/>
                           </div>
                           <p>Location received. Verifying you are within the designated work area...</p>
                      </>
                  )}
                  {geofenceStatus === 'error' && (
                      <>
                          <div className="flex justify-center my-4">
                              <X className="w-12 h-12 text-destructive"/>
                          </div>
                          <p className="text-destructive font-medium">{geofenceError}</p>
                           <Button variant="outline" onClick={() => setGeofenceStatus("idle")}>Try Again</Button>
                      </>
                  )}
              </div>
            </AlertDialogDescription>
          </AlertDialogHeader>
        </AlertDialogContent>
      </AlertDialog>
    </>
  );
}
